___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 499px) {
    .nav {
      z-index: 10;
      background-color: #fff;
      width: 300px;
      position: absolute;
  
      /* this trasform moves the drawer off canvas. */
      -webkit-transform: translate(-300px, 0);
      transform: translate(-300px, 0);
  
      /* Optionally, we animate the drawer */
      transition: transform 0.3s ease;
    }
  
    .open {
      -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
    }
  
    .nav__item {
      display: list-item;
      border-bottom: 1px solid #E0E0E0;
      width: 100%;
      text-align: left;
    }
  }
  
  @media screen and (min-width: 650px) {
    .hero {
      background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    }
  
    .app-bar {
      grid-template-columns: 1fr auto;
      padding: 8px 32px;
    }
   
    .app-bar .app-bar__brand h1 {
      font-size: 1.5em;
    }
   
    .app-bar .app-bar__menu {
      display: none;
    }
   
    .app-bar .app-bar__navigation {
      position: static;
      width: 100%;
    }
   
    .app-bar .app-bar__navigation ul li {
      display: inline-block;
    }
   
    .app-bar .app-bar__navigation ul li a {
      display: inline-block;
      width: 120px;
      text-align: center;
      margin: 0;
      min-height: 44px;
      min-width: 44px;
    }
   
    .restaurants {
      grid-template-columns: 1fr 1fr;
    }
   
    .restaurant {
      grid-template-columns: auto 1fr;
    }
   
    .restaurant .restaurant__title {
      grid-column-start: 1;
      grid-column-end: 3;
    }
   
    .restaurant .restaurant__overview {
      grid-column-start: 1;
      grid-column-end: 3;
    }
  }
   
  @media screen and (min-width: 800px) {
    .app-bar .app-bar__brand h1 {
      font-size: 2em;
    }
  }
   
  @media screen and (min-width: 850px) {
    .restaurants {
      grid-template-columns: repeat(3, 1fr);
    }
  }
   
  @media screen and (min-width: 1200px) {
    .restaurants {
      grid-template-columns: repeat(4, 1fr);
    }
  }
   
  @media screen and (min-width: 1600px) {
    .restaurants {
        grid-template-columns: repeat(5, 1fr);
    }
  }`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;IACI;MACE,WAAW;MACX,sBAAsB;MACtB,YAAY;MACZ,kBAAkB;;MAElB,+CAA+C;MAC/C,uCAAuC;MACvC,+BAA+B;;MAE/B,sCAAsC;MACtC,+BAA+B;IACjC;;IAEA;MACE,kCAAkC;MAClC,0BAA0B;IAC5B;;IAEA;MACE,kBAAkB;MAClB,gCAAgC;MAChC,WAAW;MACX,gBAAgB;IAClB;EACF;;EAEA;IACE;MACE,yDAAuE;IACzE;;IAEA;MACE,+BAA+B;MAC/B,iBAAiB;IACnB;;IAEA;MACE,gBAAgB;IAClB;;IAEA;MACE,aAAa;IACf;;IAEA;MACE,gBAAgB;MAChB,WAAW;IACb;;IAEA;MACE,qBAAqB;IACvB;;IAEA;MACE,qBAAqB;MACrB,YAAY;MACZ,kBAAkB;MAClB,SAAS;MACT,gBAAgB;MAChB,eAAe;IACjB;;IAEA;MACE,8BAA8B;IAChC;;IAEA;MACE,+BAA+B;IACjC;;IAEA;MACE,oBAAoB;MACpB,kBAAkB;IACpB;;IAEA;MACE,oBAAoB;MACpB,kBAAkB;IACpB;EACF;;EAEA;IACE;MACE,cAAc;IAChB;EACF;;EAEA;IACE;MACE,qCAAqC;IACvC;EACF;;EAEA;IACE;MACE,qCAAqC;IACvC;EACF;;EAEA;IACE;QACI,qCAAqC;IACzC;EACF","sourcesContent":["@media screen and (max-width: 499px) {\n    .nav {\n      z-index: 10;\n      background-color: #fff;\n      width: 300px;\n      position: absolute;\n  \n      /* this trasform moves the drawer off canvas. */\n      -webkit-transform: translate(-300px, 0);\n      transform: translate(-300px, 0);\n  \n      /* Optionally, we animate the drawer */\n      transition: transform 0.3s ease;\n    }\n  \n    .open {\n      -webkit-transform: translate(0, 0);\n      transform: translate(0, 0);\n    }\n  \n    .nav__item {\n      display: list-item;\n      border-bottom: 1px solid #E0E0E0;\n      width: 100%;\n      text-align: left;\n    }\n  }\n  \n  @media screen and (min-width: 650px) {\n    .hero {\n      background-image: url(\"../../src/public/images/heros/hero-image_3.jpg\");\n    }\n  \n    .app-bar {\n      grid-template-columns: 1fr auto;\n      padding: 8px 32px;\n    }\n   \n    .app-bar .app-bar__brand h1 {\n      font-size: 1.5em;\n    }\n   \n    .app-bar .app-bar__menu {\n      display: none;\n    }\n   \n    .app-bar .app-bar__navigation {\n      position: static;\n      width: 100%;\n    }\n   \n    .app-bar .app-b